export function informationAboutFollowerFetchDataSuccess(informations) {
    return {
        type: 'INFORMATIONS_FETCH_DATA_SUCCESS',
        informations
    }
}

export function informationAboutFollowerFetchData(url) {
    return(dispatch)=> {
        fetch(url)
        .then(response => {
            if(!response.ok){
                throw new Error(response.statusText)
            }
            return response
        })
        .then(response => response.json())
        .then(informations => dispatch(informationAboutFollowerFetchDataSuccess(informations)))
    }
}